import "./index.css";

export const Index = ({ className, disabled, state, onClick }) => {
  return (
    <div
      disabled={disabled}
      onClick={onClick}
      className={`btn-show ${state ? "btn-show_closed" : "btn-show_shown"} ${className}`}
    >
      <div className="btn-show__icon"></div>
    </div>
  );
};
