import "./index.css";

import React from "react";
import { Button } from "../../../components1/pieces";

export const Index = ({ className, changeHandler, quantity, currentIdx }) => {
  return (
    <div
      className={`slider-progress justify-content-center align-items-center d-flex mt-5 pt-3 ${className}`}
    >
      <Button variant="slide-left" onClick={() => changeHandler("previous")} />
      <div className="slider-progress__circles align-items-center d-flex">
        {Array.from({ length: quantity }).map((_, index) => {
          return <div className={`slider-progress__circle ${currentIdx === index ? "slider-progress__circle_active" : ""}`}></div>;
        })}
      </div>
      <Button variant="slide-right" onClick={() => changeHandler("next")} />
    </div>
  );
};
