import "./index.css";

import { useState, useRef, useContext } from "react";
import { Context } from "../../../Context";
import { Button } from "../";
import useOnClickOutside from "../../../hooks/useOnClickOutside";
import usePosition from "../../../hooks/usePosition";
import { GoogleMap, useJsApiLoader } from '@react-google-maps/api';

export const Index = ({ className }) => {
  const [shown, setShown] = useState(true);
  const confirmCity = useRef();

  useOnClickOutside(confirmCity, () => setShown(false));

  const handleClose = () => {
    setShown(false);

    navigator.geolocation.getCurrentPosition((position) => {
      let lat = position.coords.latitude;
      let long = position.coords.longitude;
      // new GoogleMap.Geocoder().geocode(
      //   {
      //     latLng: new GoogleMap.LatLng(lat.toFixed(2), long.toFixed(2)),
      //   },
      //   function (results, status) {
      //     if (status == GoogleMap.GeocoderStatus.OK) {
      //       console.log(results);
      //     }
      //   }
      // );
    });
  };

  return (
    <div
      className={`confirm-city ${className} align-items-center ${
        shown ? "d-xl-flex" : "d-none"
      }`}
      ref={confirmCity}
    >
      <div className="confirm-city__text">Ваш город Москва?</div>
      <Button
        variant="blue"
        className="ml-3 text-xxxs pt-0 pb-0 px-3"
        onClick={handleClose}
      >
        Да
      </Button>
      <Button
        variant="white"
        className="ml-2 text-xxxs pt-0 pb-0 px-3 confirm-city__btn_change"
        onClick={handleClose}
      >
        Изменить
      </Button>
    </div>
  );
};
