import "./index.css";

import React, { memo, useContext, useMemo } from "react";
import { useState, useEffect } from "react";
import manHand from "../../assets/images/modal/manHand.svg";
// import { PhoneInput } from "../../components1/parts";
import { Button } from "../../components1/pieces";
import { Context } from "../../Context";
import { Index as SocialMedia } from "./";
import vk from "../../assets/images/modal/vk.svg";
import wa from "../../assets/images/modal/wa.svg";
import tg from "../../assets/images/modal/tg.svg";
import yt from "../../assets/images/modal/yt.svg";
import submitPhone from "../../functions/submitPhone";
import { PhoneInput } from "../../components1/parts/PhoneInput";

export const Index = () => {
  const [context, setContext] = useContext(Context);
  const [visited, setVisited] = useState(false);
  const [gotText, setGotText] = useState("");
  const [hovered, setHovered] = useState(false)

  const checkDoesWeWork = () => {
    const hours = new Date().getHours();
    const minutes = new Date().getMinutes();
    if (hours * 60 + minutes >= 540 && hours * 60 + minutes <= 1180)
      return true;
    return false;
  };

  useEffect(() => {
    if (checkDoesWeWork())
      setGotText("Готово! Свяжемся с вами в течение 5 минут.");
    if (!checkDoesWeWork())
      setGotText(
        "Готово! Сейчас мы не работаем, свяжемся с вами с 9:00 до 20:00."
      );
  }, []);

  useEffect(() => {
    if (!visited) {
      const updateMousePosition = (ev) => {
        if (ev.clientX < 10 || ev.clientY < 10) {
          setContext({ ...context, modal: true });
          setVisited(true);
        }
      };
      window.addEventListener("mousemove", updateMousePosition);
      return () => {
        window.removeEventListener("mousemove", updateMousePosition);
      };
    }
  }, [visited]);

  const onClose = () => {
    setVisited(true);
    setContext({ ...context, modal: false });
  };

  const useStage = () => {
    if (context.modalStage === "")
      setContext({ ...context, modalStage: "phone" });
    else if (context.modalStage === "phone") {
      setContext({ ...context, modalStage: "got" });
    }
  };

  return (
    <div
      className={`modal d-flex ${
        context.modal ? "modal_shown" : "modal_closed"
      }`}
    >
      <div
        className={`modal__content background-grey m-auto d-flex ${
          context.modal ? "modal__content_shown" : "modal__content_closed"
        }`}
      >
        <div className="modal__offer px-0 d-flex flex-column">
          {(context.modalStage === "" && (
            <>
              <div className="text-xxxl modal-title bolder px-0">
                Поможем решить ваш<br></br> вопрос с недвижимостью
              </div>
            </>
          )) ||
            (context.modalStage === "phone" && (
              <>
                <div className="text-xxxl modal-title bolder px-0">
                Свяжемся с вами и поможем<br></br> найти решение
                </div>
                <PhoneInput className="modal__input mt-auto text-m" />
              </>
            )) ||
            (context.modalStage === "got" && (
              <>
                <div className="text-xl modal__title_underlined bolder col-xl-5 px-0">{gotText}</div>
                {/* <div className="text-s mt-auto">Посмотрите наши соцсети</div> */}
                <div className="modal__social-media mt-auto social-media px-0 d-flex align-items-center">
                  <a>
                    <img src={vk} width={50} className="modal__soc"/>
                  </a>
                  <a href="https://t.me/a_kolotii" className="modal__soc">
                    <img src={tg} width={45} />
                  </a>
                  <a href="https://t.me/a_kolotii" className="modal__soc">
                    <img src={yt} width={50} />
                  </a>
                  <a className="modal__soc">
                    <img src={wa} width={45} />
                  </a>
                </div>
                <Button
                  variant="white"
                  className="d-none text-m mt-4"
                  onClick={onClose}
                >
                  Вернуться на главную
                </Button>
              </>
            ))}
          <div
            className={`modal__buttons  ${
              context.modalStage === "phone"
                ? "d-flex flex-column flex-lg-row modal__buttons_phone_stage mt-4"
                : context.modalStage === "got"
                ? "d-none"
                : "d-flex flex-column mt-lg-auto mt-5"
            }`}
          >
            <Button
              onClick={useStage}
              className={`text-m bold ${hovered ? "btn_hovered" : ""}`}
              variant="blue"
              disabled={false}
            >
              {context.modalStage === "phone"
                ? "Позвоните мне"
                : "Бесплатная консультация"}
            </Button>
            <Button
              className={`text-m bold mt-3 ${
                context.modalStage === "phone" ? "ml-lg-3 mt-lg-0" : ""
              }`}
              variant="white"
              onClick={onClose}
              onMouseEnter={() => setHovered(true)}
              onMouseLeave={() => setHovered(false)}
              disabled={false}
            >
              {context.modalStage === "phone" ? (
                <>
                  <span className="d-block d-lg-none">Написать в WhatsApp</span>
                  <span className="d-none d-lg-block">Напишите в WhatsApp</span>
                </>
              ) : (
                "Нет, спасибо"
              )}
            </Button>
          </div>
        </div>
        {(context.modalStage === "" || context.modalStage === "got") && (
          <img
            src={manHand}
            className="modal__img ml-auto mr-3 d-none d-lg-block mt-auto"
          />
        )}
        <Button onClick={onClose} className="modal__close" variant={"close"} />
      </div>
    </div>
  );
};
