import "./index.css";
import { useState } from "react";

export const Index = ({ className, text, disabled, onClick, ...props }) => {
  const [anim, setAnim] = useState(false);
  const handleClick = () => {
    setAnim(true);
    onClick();
  };
  return (
    <button
      disabled={disabled}
      className={`btn-slide ${className}`}
      onClick={handleClick}
      {...props}
    >
      <div className="btn-slide__icon">
        {/* <div className="btn-slide__stick"></div>
        <div className="btn-slide__stick"></div> */}
      </div>
      <div
        className={`btn-slide__border ${anim ? "btn-slide__border_anim" : ""}`}
        onAnimationEnd={() => setAnim(false)}
      ></div>
    </button>
  );
};
