import "./index.css";
import {
  BtnSlide,
  BtnOpen,
  BtnSwitch,
  BtnDefault,
  BtnClose,
} from "../../buttons/index";

export const Index = ({
  variant,
  value,
  className,
  onClick,
  disabled,
  state,
  ref,
  ...props
}) => {
  return (
    <>
      {(variant === "slide-right" && (
        <BtnSlide
          className={`btn-slide-right ${className}`}
          onClick={onClick}
          text={props.children}
          disabled={disabled}
        />
      )) ||
        (variant === "slide-left" && (
          <BtnSlide
            className={`btn-slide-left ${className}`}
            onClick={onClick}
            text={props.children}
            disabled={disabled}
          />
        )) ||
        (variant === "open" && (
          <BtnOpen
            className={`${className}`}
            onClick={onClick}
            text={props.children}
            state={state}
            disabled={disabled}
          />
        )) ||
        (variant === "close" && (
          <BtnClose
            className={`${className}`}
            onClick={onClick}
            text={props.children}
            state={state}
            disabled={disabled}
          />
        )) ||
        (variant === "switch" && (
          <BtnSwitch
            value={value}
            className={`${className}`}
            onClick={onClick}
            text={props.children}
            disabled={disabled}
          />
        )) ||
        (variant === "blue" && (
          <BtnDefault
            className={`btn_blue ${className}`}
            onClick={onClick}
            text={props.children}
            disabled={disabled}
            onMouseEnter={props.onMouseEnter}
            onMouseLeave={props.onMouseLeave}
          />
        )) ||
        (variant === "white" && (
          <BtnDefault
            onMouseEnter={props.onMouseEnter}
            className={`btn_white ${className}`}
            onClick={onClick}
            text={props.children}
            disabled={disabled}
            onMouseLeave={props.onMouseLeave}
          />
        ))}
    </>
  );
};
