import "./index.css";
import { useState, useEffect, useReducer, useContext } from "react";
import InputMask from "react-input-mask";
import { Phone } from "../../../Context";
export const PhoneInput = ({ className, submit }) => {
  const [phoneContext, setPhoneContext] = useContext(Phone);
  const [phone, setPhone] = useState({ user_phone: "" });
  const [wrong, setWrong] = useState(false);

  const changeHandler = (e) => {
    setPhone({ user_phone: e.target.value });
    setPhoneContext({ phone: e.target.value });
  };

  const [mask, setMask] = useState("*9 (999) 999 99 99");

  const beforeMaskedValueChange = (newState) => {
    let { value } = newState;
    let selection = newState.selection;
    let cursorPosition = selection ? selection.start : null;

    if (phone.user_phone === "") setMask("*9 (999) 999 99 99");

    if (value.length < 4 && phone.user_phone.startsWith("8")) {
      value = "+7 (";
    }

    if (value.length < 4 && phone.user_phone.startsWith("+")) {
      setMask("*9 (999) 999 99 99");
    }

    if (value.length < 4 && phone.user_phone.startsWith("7")) {
      setMask("+7 (999) 999 99 99");
    }

    if (
      value.length < 4 &&
      (phone.user_phone.startsWith("9") ||
        phone.user_phone.startsWith("3") ||
        phone.user_phone.startsWith("4"))
    ) {
      setMask("+7 (999) 999 99 99");
    }

    selection = { start: cursorPosition + 1, end: cursorPosition + 1 };

    return {
      value,
      selection,
    };
  };

  return (
    <InputMask
      onChange={changeHandler}
      mask={mask}
      maskChar={null}
      placeholder="+7 (909) 000 00 00"
      name="phone"
      permanents={[]}
      formatChars={{
        9: "[0-9]",
        "*": "[+, 8, 7, 9, 4, 3]",
      }}
      value={phone.user_phone}
      className={`input input_phone text-l pb-3 pt-3 pl-4 pr-5 phone-form__input ${className} ${
        wrong && "input_wrong"
      }`}
      beforeMaskedValueChange={beforeMaskedValueChange}
    />
  );
};
