import "./index.css";

export const Index = ({
  className,
  text,
  onClick,
  disabled,
  onMouseEnter,
  onMouseLeave,
}) => {
  return (
    <button
      disabled={disabled}
      onMouseLeave={onMouseLeave}
      onMouseEnter={onMouseEnter}
      className={`btn ${className}`}
      onClick={onClick}
    >
      {text}
    </button>
  );
};
